import { FC } from "react";
import { WidthInterface } from "./helper";
import styled from "@emotion/styled";

export const AboutUsSection:FC<WidthInterface> = ({data}) => {

    
    return(
        <AboutUs>
            <Header>
                KIM JESTEMY?
            </Header>
            <ContentContainer  id="products">
                <p>
                    Od ponad 20 lat projektujemy i produkujemy  nowe rozwizania dla budownictwa, przemysu i domu.
                    Upraszczamy i optymalizujemy Twoj prac dziki opatentowanym rozwizaniom, ktre docenili uytkownicy na caym wiecie. 
                </p>
            </ContentContainer>
        </AboutUs>
    )
}


const AboutUs = styled.div`
    margin-top: 5vh;
    min-height: 30vh;
    width: 100%;
`;

const Header = styled.h1`
    display: flex;
    align-items: center;
    justify-content: center;
`;

const ContentContainer = styled.div`
    display: flex; 
    align-items: center;
    justify-content: center;

    p {
        margin-top: 2vh;
        width: 50%;
        text-align: center;
        font-size: 150%;
    }
`;